<template>
	<view class="bigBox">
		<view class="title">
			请选择方式
		</view>
		<view style="display: flex; margin: 30rpx; justify-content: center; justify-content: space-around;">
			<view v-for="(item,index) in typeList" class="typeItem" @click="selectItem(item,index)" :style="[item.active?{background:'linear-gradient(to top, #a6e3e9, #e3fdfd)'}:{}]">
				{{item.name}}
			</view>
		</view>
		<button @click="visible = true">点击</button>
		<describe-vue :show="visible" :content="content" @close="closeDescribe" ></describe-vue>
		<one-vue v-show="selectIndex == 0"></one-vue>
		<two-vue v-show="selectIndex == 1"></two-vue>
		<three-vue v-show="selectIndex == 2"></three-vue>
		
	</view>
</template>


<style>
	.typeItem{
		width: 120rpx;
		height: 120rpx;
		font-size: 30rpx;
		background-color: white;
		text-align: center;
		line-height: 120rpx;
		border-radius: 20rpx;
		border: 1rpx solid gray;
		box-shadow: 10px 10px 10px -4px rgba(128, 128, 128, 0.5);
	}
	.title{
		border-radius: 20rpx  200rpx;
		padding: 20rpx;
		font-size: 35rpx;
		font-weight: bold;
		background-color: #ddff93;
	}
	.bigBox{
		height: 100vh;
	}
</style>


<script>
	import oneVue from './componment/one.vue'
	import twoVue from './componment/two.vue'
	import threeVue from './componment/three.vue'
	import describeVue from './componment/describe.vue'
	export default {
		components:{oneVue,twoVue,threeVue,describeVue},
		data(){
			return {
				visible:true,
				
				content:`
				第一
				第二
				第三
				第四
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				第五
				`,
				selectIndex:0,
				typeList:[
					{
						id:1,
						name:'自销',
						active:true,
					},
					{
						id:2,
						name:'代销',
						active:false,
					},
					{
						id:2,
						name:'批发',
						active:false,
					}
				]
			}
		},
		methods:{
			closeDescribe(){
				this.visible = false
				console.log(11111)
			},
			selectItem(item,index){
				this.typeList.filter(item=>{item.active  = false})
				this.selectIndex = index
				item.active = true
			}
		}
	}
</script>

